#home {
    position: relative;
    // background: url("../../assets/bgIMG.png");
    background-size: cover;
    background-position: center;
    background-repeat: repeat;

    // disable the default padding styles
    .app__wrapper {
        padding: 0;

        .copyright {
            display: none;
        }
    }
}

.app__header {
    flex: 1;
    width: 100%;
    height: 100%;
    flex-direction: row;

    padding: 6rem 2rem 0rem;
    // background-image: url("../../assets/mermaid.jpg");
    background-size: cover;
    background-position: bottom;
    background-repeat: no-repeat;
    border-radius: 4px;

    @media screen and (min-width: 2000px) {
        padding-top: 8rem;
    }

    @media screen and (max-width: 1200px) {
        flex-direction: column;
    }

    @media screen and (max-width: 450px) {
        padding: 6rem 1rem 2rem;
    }
}

.app__header-info {
    flex: 0.65;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    height: 100%;

    margin: 0;

    @media screen and (max-width: 1200px) {
        width: 100%;
    }
}

.app__header-badge {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;

    .badge-cmp,

    .tag-cmp {
        padding: 1rem 2rem;
        color: var(--gray-color);
        border-radius: 7px;
        flex-direction: row;
        width: auto;

        // box-shadow: 0px 0px 20px #dce4f5;
    }

    .tag-cmp {
        flex-direction: column;
        margin-top: 1.2rem;

        p {
            width: 100%;
            text-transform: uppercase;
            text-align: right;
            letter-spacing: .23rem;
            font-size: 1.12rem;
        }
    }

    span {
        font-size: 2.5rem;
        background: #d0b6774d;
        border-radius: 50%;

        @media screen and (min-width: 2000px) {
            font-size: 5rem;
        }
    }

    @media screen and (max-width: 1200px) {
        justify-content: center;
        align-items: center;
    }
}

.app__header-circles {
    flex: 0.75;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    height: 100%;
    margin-left: 1rem;

    div {
        width: 100px;
        height: 100px;
        border-radius: 50%;

        background: var(--white-color);
        box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);

        img {
            width: 60%;
            height: 60%;
        }
    }

    // circles with different height & width

    div:nth-child(1) {
        width: 60px;
        height: 60px;
    }

    div:nth-child(2) {
        margin: .75rem;
        width: 120px;
        height: 120px;
    }

    div:nth-child(3) {
        width: 80px;
        height: 80px;
    }

    @media screen and (min-width: 2000px) {
        div:nth-child(2) {
            width: 400px;
            height: 400px;
        }

        div:nth-child(3) {
            width: 170px;
            height: 170px;
        }

        div:nth-child(1) {
            width: 110px;
            height: 110px;
        }
    }

    @media screen and (max-width: 1200px) {
        width: 100%;
        flex-direction: row;
        flex-wrap: wrap;
        margin-left: 0;

        div {
            margin: 1rem;
        }
    }
}

.ompa {
    color: #8eb94f !important;
}

.app__header-img {
    flex: 1;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    @media screen and (min-width: 768px) {
        width: 60%;
    }

    @media screen and (min-width: 1200px) {
        height: 80%;
    }

    img {
        object-fit: contain;
        z-index: 1;
        height: auto;
        width: 80%;




        @media screen and (min-width: 768px) {
            width: 72%;
        }


    }

    .overlay_circle {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 0;
        width: 100%;
        height: auto;

        @media screen and (min-width: 768px) {
            width: 82%;

        }

        @media screen and (min-width: 1024px) {
            margin-top: -11rem;

        }

        @media screen and (min-width: 1440px) {
            width: auto;
            height: 90%;

        }

    }

    @media screen and (max-width: 1200px) {
        margin: 2rem 0;
    }
}

.pro_img {
    // background-image: url("../../assets/x.png");
    // background-size: cover;
    // background-position: top;
    // background-repeat: no-repeat;
    border-radius: 4px;

    @media screen and (max-width: 900px) {}

    @media screen and (min-width: 1200px) {
        background: none;
        height: 100%;
        border-radius: 50%;
    }
}